import React from 'react';
import PropTypes from 'prop-types';
import { Doughnut } from 'react-chartjs-2';
import { useTheme } from '@material-ui/core';

const chartColors = ['#CCC', '#36A2EB', '#FFCE56', '#66FF66', '#00DDAA'];
const chartHoverColors = ['#CCC', '#36A2EB', '#FFCE56', '#66FF66', '#00DDAA'];

function Chart({ data: dataProp, ...rest }) {
  const theme = useTheme();

  const data = {
    labels: dataProp.map((value) => value.name),
    datasets: [
      {
        data: dataProp.map((value) => value.members_count),
        backgroundColor: chartColors,
        hoverBackgroundColor: chartHoverColors
      }
    ],
    borderWidth: 8,
    borderColor: theme.palette.background.default,
    hoverBorderColor: theme.palette.background.default
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    animation: false,
    cutoutPercentage: 80,
    legend: {
      display: false
    },
    layout: {
      padding: 0
    },
    tooltips: {
      enabled: true,
      mode: 'index',
      intersect: false,
      caretSize: 10,
      yPadding: 20,
      xPadding: 20,
      borderWidth: 1,
      borderColor: theme.palette.divider,
      backgroundColor: theme.palette.background.dark,
      titleFontColor: theme.palette.text.primary,
      bodyFontColor: theme.palette.text.secondary,
      footerFontColor: theme.palette.text.secondary,
      callbacks: {
        label(tooltipItem, _data) {
          const label = _data.labels[tooltipItem.index];
          const value = _data.datasets[0].data[tooltipItem.index];

          return `${label}: ${value}%`;
        }
      }
    }
  };

  return <Doughnut data={data} options={options} {...rest} />;
}

Chart.propTypes = {
  className: PropTypes.string,
  data: PropTypes.array.isRequired
};

export default Chart;
