import React, { useEffect, useState, useCallback } from 'react';
import { Container, Grid, makeStyles } from '@material-ui/core';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import Page from '../../../components/Page';
import Header from './Header';
import ConsumeChopCount from './ConsumeChopCount';
import TotalChopCount from './TotalChopCount';
import EarnChopCount from './EarnChopCount';
import BranchCount from './BranchCount';
import MemberCount from './MemberCount';
import TotalAmount from './TotalAmount';
import Topup from './Topup';
import Payment from './Payment';
import RankMembers from './RankMembers';
import reportService from '../../../services/reportService';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  },
  container: {
    [theme.breakpoints.up('lg')]: {
      paddingLeft: 64,
      paddingRight: 64
    }
  }
}));

const initialValues = {
  member_count: 0,
  branch_count: 0,
  manual_add_chops: 0,
  earn_chops: 0,
  consume_chops: 0,
  total_chops: 0,
  rank_members: []
};

function DashboardView() {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [data, setData] = useState(initialValues);

  const getData = useCallback(async () => {
    const report = await reportService.dashboard();
    setData(report.data.data);
  }, [isMountedRef]);

  useEffect(() => {
    getData();
  }, [getData]);

  return (
    <Page className={classes.root} title="Dashboard">
      <Container maxWidth={false} className={classes.container}>
        <Header />
        <Grid container spacing={3}>
          <Grid item lg={3} sm={6} xs={12}>
            <MemberCount data={data.member_count} />
          </Grid>
          <Grid item lg={3} sm={6} xs={12}>
            <BranchCount data={data.branch_count} />
          </Grid>
          <Grid item lg={3} sm={6} xs={12}>
            <EarnChopCount data={data.manual_add_chops + data.earn_chops} />
          </Grid>
          <Grid item lg={3} sm={6} xs={12}>
            <ConsumeChopCount data={data.consume_chops} />
          </Grid>
          <Grid item lg={3} sm={6} xs={12}>
            <TotalChopCount data={data.manual_add_chops + data.earn_chops - data.total_chops} />
          </Grid>
          <Grid item lg={3} sm={6} xs={12}>
            <TotalAmount data={data.total_amount} />
          </Grid>
          <Grid item lg={3} sm={6} xs={12}>
            <Topup data={data.total_topup} />
          </Grid>
          <Grid item lg={3} sm={6} xs={12}>
            <Payment data={data.total_paymnet} />
          </Grid>
          <Grid item lg={12} xl={12} xs={12}>
            <RankMembers data={data.rank_members} />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}

export default DashboardView;
